import React from 'react';
import Carousel from 'react-bootstrap/Carousel';
import ReactPlayer from 'react-player';
import video1 from '../videos/video1.mp4';
import video2 from '../videos/video2.mp4';
import video3 from '../videos/video3.mp4';
import video4 from '../videos/video4.mp4';
import video5 from '../videos/video5.mp4';

const videos = [
  { src: video1, caption: 'Primera Generacin (1940-1956)', link: 'https://www.instagram.com/' },
  { src: video2, caption: 'Segunda Generacin (1956-1963)', link: 'https://www.instagram.com/' },
  { src: video3, caption: 'Tercera Generacin (1964-1971)', link: 'https://www.instagram.com/' },
  { src: video4, caption: 'Cuarta Generacin (1971-1980)', link: 'https://www.instagram.com/' },
  { src: video5, caption: 'Quinta Generacin (1980-presente)', link: 'https://www.instagram.com/' },
];

const CarouselComponent = () => {
  return (
    <Carousel>
      {videos.map((video, index) => (
        <Carousel.Item key={index}>
          <ReactPlayer url={video.src} controls width="100%" height="100%" />
          <Carousel.Caption>
            <h3>{video.caption}</h3>
            <p>
              <a href={video.link} target="_blank" rel="noopener noreferrer">
                <button className="btn btn-primary">Me se seria de ayuda que me sigas al insta </button>
              </a>
            </p>
          </Carousel.Caption>
        </Carousel.Item>
      ))}
    </Carousel>
  );
};

export default CarouselComponent;
