// Page10.js
import React, { useState } from 'react';
import { Form, Button, Alert } from 'react-bootstrap';

const Page10 = () => {
  const [nombre, setNombre] = useState('');
  const [email, setEmail] = useState('');
  const [mensaje, setMensaje] = useState('');
  const [formularioEnviado, setFormularioEnviado] = useState(false);

  const handleSubmit = (event) => {
    event.preventDefault();
    // Aqu puedes agregar la lgica para enviar los datos a tu backend o realizar otras acciones
    console.log('Formulario enviado:', { nombre, email, mensaje });

    // Actualiza el estado para mostrar el mensaje de agradecimiento
    setFormularioEnviado(true);
  };

  return (
    <div>
      <h2>Formulario sobre Generacin de Computadoras</h2>
      {formularioEnviado ? (
        <Alert variant="success">
          Gracias por tu aporte!
        </Alert>
      ) : (
        <Form onSubmit={handleSubmit}>
          <Form.Group controlId="formNombre">
            <Form.Label>Nombre:</Form.Label>
            <Form.Control
              type="text"
              placeholder="Ingresa tu nombre"
              value={nombre}
              onChange={(e) => setNombre(e.target.value)}
            />
          </Form.Group>

          <Form.Group controlId="formEmail">
            <Form.Label>Email:</Form.Label>
            <Form.Control
              type="email"
              placeholder="Ingresa tu email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </Form.Group>

          <Form.Group controlId="formMensaje">
            <Form.Label>Mensaje:</Form.Label>
            <Form.Control
              as="textarea"
              rows={4}
              placeholder="Podras darnos tu aporte?"
              value={mensaje}
              onChange={(e) => setMensaje(e.target.value)}
            />
          </Form.Group>

          <Button variant="primary" type="submit">
            Enviar
          </Button>
        </Form>
      )}
    </div>
  );
};

export default Page10;
