import React from 'react';
import { Link } from 'react-router-dom';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

const Header = () => {
  return (
    <header>
      <Navbar bg="dark" variant="dark" expand="lg">
        <Navbar.Brand as={Link} to="/">GENERACIONES DE COMPUTADORAS
</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link as={Link} to="/page1">Primera generacion</Nav.Link>
            <Nav.Link as={Link} to="/page2">Segunda Generacion</Nav.Link>
            <Nav.Link as={Link} to="/page3">Tercera generacion</Nav.Link>
            <Nav.Link as={Link} to="/page4">Cuarta generacion</Nav.Link>
            <Nav.Link as={Link} to="/page5">Quinta Generacion</Nav.Link>
            <Nav.Link as={Link} to="/page6">ENIAC</Nav.Link>
            <Nav.Link as={Link} to="/page7">UNIVAC</Nav.Link>
            <Nav.Link as={Link} to="/page8">Inteligencia Artificial</Nav.Link>
            <Nav.Link as={Link} to="/page9">VIDEOS EXTRA</Nav.Link>
            <Nav.Link as={Link} to="/page10">Ayudanos</Nav.Link>

            {/* Agrega ms enlaces segn tus necesidades */}
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      
    </header>
  );
};

export default Header;
