import React from 'react';
import Carousel from 'react-bootstrap/Carousel';
import PRIMERA from '../imagenes/PRIMERA.jpg';
import SEGUNDA from '../imagenes/SEGUNDA.jpg';
import TERCERA from '../imagenes/TERCERA.jpg';
import CUARTA from '../imagenes/CUARTA.jpg';
import imagen1 from '../imagenes/imagen1.jpg';

const CarouselComponent = () => {
  return (
    <Carousel>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={PRIMERA}
          alt="Primera imagen"
        />
        <Carousel.Caption>
          <h3>Primera Generacion</h3>
          <p>(1940-1956).</p>
<a href="https://www.youtube.com/watch?v=AuMVvq-h5Mo" target="_blank" rel="noopener noreferrer">
          <button className="btn btn-primary">un video educativo aqui</button>
          </a>         
          
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={SEGUNDA}
          alt="Segunda imagen"
        />
        <Carousel.Caption>
          <h3>Segunda Generacin </h3>
          <p>(1956-1963)</p>
<a href="https://www.youtube.com/watch?v=MukEdpELaMM" target="_blank" rel="noopener noreferrer">
          <button className="btn btn-primary">un video educativo aqui</button>
          </a>         
         
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={TERCERA}
          alt="Tercera imagen"
        />
        <Carousel.Caption>
          <h3>Tercera Generacin </h3>
          <p>(1964-1971)</p>
<a href="https://www.youtube.com/watch?v=eFbsOdIzj7w" target="_blank" rel="noopener noreferrer">
          <button className="btn btn-primary">un video educativo aqui</button>
          </a>       
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
      
      <img
          className="d-block w-100"
          src={CUARTA}
          alt="Tercera imagen"
        />
        <Carousel.Caption>
          <h3> Cuarta Generacin </h3>
          <p>(1971-1980)</p>
<a href="https://www.youtube.com/watch?v=ykJ34GgzW4A" target="_blank" rel="noopener noreferrer">
          <button className="btn btn-primary">un video educativo aqui</button>
          </a>  
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
      <img
      className="d-block w-100"
      src={imagen1}
      alt="Tercera imagen"
    />
    <Carousel.Caption>
      <h3>Quinta Generacin </h3>
      <p>(1980-presente)</p>
      <a href="https://www.youtube.com/watch?v=BTt-xDqLctk" target="_blank" rel="noopener noreferrer">
          <button className="btn btn-primary">un video educativo aqui</button>
          </a>  
    </Carousel.Caption>
  </Carousel.Item>
      {/* Agrega ms items segn la cantidad de imgenes que tengas */}
    </Carousel>
  );
};

export default CarouselComponent;
