import React from 'react';
import UNIVAC from '../imagenes/UNIVAC.jpg';
import ENIAC from '../imagenes/ENIAC.jpg';
const Page1 = () => {
  return (
    <div>
      <h2>Primera Generacin de Computadoras</h2>
      <p>En esta pgina, explora la era de las computadoras de bulbos y vlvulas de vaco.</p>
      <p>Un tambor magntico, es un cilindro de metal recubierto con material
magntico de xido de hierro en que los datos y los programas pueden ser
almacenados. Estos eran utilizados como dispositivos de almacenamiento primario,
pero desde entonces se han implementado como dispositivos de almacenamiento
auxiliar.
La operacin de estos equipos era de alto costo y adicionalmente consuman
gran cantidad de electricidad, generaban mucho calor la cual era la causa de mal
funcionamiento. La primera generacin de computadoras se basaba en lenguaje
mquina para realizar operaciones y solo podan resolver un tiempo a la vez.
Los computadores UNIVAC y ENIAC son ejemplos de componentes de la
primera generacin. LA UNIVAC fue la primera computadora comercial entregada
a un cliente.
El 14 de febrero de 1946, J.P. Eckert y John Mauchly, presentaron el ENIAC,
que resolva cinco mil adiciones y sustracciones, trescientos cincuenta
multiplicaciones o cincuenta divisiones por segundo. Tena cerca de 19.000
vlvulas sustituidas por ao. Esta mquina pesaba 30 toneladas, usaba 200 kW y
consista de 18000 tubos al vaco, 1500 relays y cientos de miles de resistores,
capacitores e inductores. En 1943, antes de la entrada en operacin del ENIAC
Inglaterra ya posea el Colossus, mquina creada por Turing para descifrar los
cdigos secretos alemanes.</p>
<div className="row">
        <div className="col-md-6 mb-3">
          <a href="https://www.computerhistory.org/revolution/early-computer-companies/5/100" target="_blank" rel="noopener noreferrer">
            <img
              src={UNIVAC} 
              alt="Descripcin de la primera imagen"
              className="img-fluid"
            />
            <p>Descripcin de la primera imagen</p>
          </a>
        </div>
        <div className="col-md-6 mb-3">
          <a href="https://en.wikipedia.org/wiki/ENIAC" target="_blank" rel="noopener noreferrer">
            <img
              src={ENIAC}  
              alt="Descripcin de la segunda imagen"
              className="img-fluid"
            />
            <p>Descripcin de la segunda imagen</p>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Page1;
